@import url(https://fonts.googleapis.com/css2?family=Amiri:ital,wght@1,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,700);
@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&family=Patua+One&family=Ubuntu+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Langar&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Narrow&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster+Two&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Potta+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Karla:ital,wght@1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #fffafa;
}
body {
  background-color: #fffafa;
  font-family: "Patua One", sans-serif;
}
nav {
  color: #a9211d;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}
.div-bg {
  background-color: #ebecf0;
}

li .nav-link {
  font-size: 1.5rem;
  color: #4286f4;
}
li .nav-link:hover {
  font-size: 1.9rem;
}
a:link {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:active {
  text-decoration: none;
}
.navbar-color {
  background-color: #47484c;
  border-bottom: 10px solid #25d366;
  margin-bottom: 0%;
}

.nav1 {
  font-family: "Oswald";
  background-color: #c9f6ff;
  margin: 0%;
  font-size: 30px;
}
.nav1-icon {
  color: #25d366;
}
.header-img {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  height: 70vh;
  width: 100%;
  margin-right: 0%;
}
.green {
  position: absolute;
  left: 20rem;
  top: 25%;
  height: 80vh;
  width: 120vh;
}
.top-right {
  position: absolute;
  top: 16rem;
  right: 4rem;
}

.paragraph {
  font-family: "Pacifico One", sans-serif;
  color: #2e8b57;
  font-weight: 200;
  font-size: 20px;
}
::-webkit-input-placeholder {
  font-size: 16px;
}
:-ms-input-placeholder {
  font-size: 16px;
}
::placeholder {
  font-size: 16px;
}
form .input {
  border: 1px #a9211d;
}
form {
  background-color: rgba(201, 246, 255, 0.767);
  box-shadow: 0 0 0 14px rgba(46, 139, 87, 0.767);
  font-size: 20px;
  font-family: "Oswald";
}
.flex-container {
  display: flex;
  flex-direction: row;
}

.fa-bars {
  color: #25d366;
}
.header-text1 {
  position: relative;
}
.header-text {
  position: absolute;
  font-size: 45px;
  font-family: "Big Shoulders Display", sans-serif;
  top: 10%;
  color: darkblue;
}
.header-txt {
  position: absolute;
  font-size: 45px;
  font-family: "Big Shoulders Display", sans-serif;
  top: 10%;
  color: #faed27;
}

.heading {
  font-size: 30px;
  font-family: "Big Shoulders Display";
  color: #a9211d;
  text-decoration: underline;
  text-transform: uppercase;
  word-spacing: 3pt;
  letter-spacing: 0.5pt;
}

footer {
  height: 12vh;
  padding-top: 4vh;
  background-color: #47484c;
  color: #25d366;
  font-size: 2.5vh;
  font-family: "Poppins", "sans-serif";
}
.list {
  color: black;
  font-size: 18px;
  font-family: "Patua One", sans-serif;
}
.warranty {
  height: 50vh;
  width: 80vh;
}
.form-phone {
  display: none;
}
.btn-primary {
  color: #4286f4;
  background-color: #c9f6ff;
  font-size: 18px;
  width: 13rem;
  height: 3rem;
  text-shadow: none;
  margin: 3px;
}
.card-title {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: #a9211d;
}
.sellcontainer {
  border: 2px solid black;
  padding-top: 95px;
}
#menu {
  right: 90%;
  top: 80%;
  width: 8em;
  color: #25d366;
  position: fixed;
  z-index: 1;
}
#menu :hover {
  position: relative;
  animation: mymove 5s infinite;
  animation-timing-function: linear;
}
.blink_me {
  animation: blinker 1s linear infinite;
  color: red;
}
#bottomNav {
  display: none;
}
.batteryProd {
  height: 75vh;
  width: 75vh;
}
.battery {
  font-family: "PT Sans Narrow", sans-serif;
  font-size: 40px;
  text-shadow: #4286f4;
  color: #47484c;
  text-decoration: underline;
}
.glyph {
  font-size: 40px;
}
footer {
  position: relative;
  bottom: 0;
  width: 100%;
}
.text-danger {
  font-size: 12px;
}
.ml {
  font-weight: 900;
  font-size: 3.5em;
}

.ml .letter {
  display: inline-block;
  line-height: 1em;
}
.blog-heading {
  font-size: 2rem;
  font-family: "Big Shoulders Display";
  color: #a9211d;
  text-decoration: underline;
  text-transform: uppercase;
  word-spacing: 3pt;
  letter-spacing: 0.5pt;
}
.blog-list-heading {
  font-size: 1.6rem;
  font-family: "Patua One";
  margin-top: 3vw;
  color: #25d366;
}
.blog-paragraph-heading {
  font-size: 1.3rem;
  font-family: "Karla";
  margin-top: 2vw;
  color: #47484c;
}
.blog-img {
  height: 32rem;
  width: 100%;
}
.haks {
  height: 12rem;
  width: 12rem;
  border: 1px solid #47484c;
}

.card {
  margin-top: 2rem;
  justify-content: center;
  width: 100%;
  margin-bottom: 2rem;
  border: 5px solid #47484c;
}
.postCard {
  box-shadow: 10px 10px 10px #47484c;
  border-bottom-left-radius: 50px;
  height: 50rem;
}
.card-img-top {
  width: 100%;
  height: 18rem;
}
@media (max-width: 992px) {
  .form-phone {
    align-items: center;
    display: inline-block;
  }
  .formtab {
    display: none;
  }
}
@media (max-width: 768px) {
  .header-img {
    height: 75vh;
    width: 100%;
  }
  .top-right {
    position: absolute;
    top: 19rem;
    right: 0.2rem;
  }
  .nav1 {
    font-size: 15px;
  }
  .heading {
    font-size: 28px;
  }
  .form-phone {
    display: none;
  }
  #bottomNav {
    display: none;
  }
  #botNav {
    display: none;
  }
  #navMob {
    display: none;
  }
  .paragraph {
    font-weight: 300;
  }
  .batteryProd {
    height: 60vh;
    width: 60vh;
  }
  .battery {
    font-size: 25px;
  }
  .glyph {
    font-size: 35px;
  }
  footer {
    position: relative;
    bottom: 0;
    left: 0;
    bottom: 0;
    width: 100%;
  }
  .card {
    margin-left: 0px;
  }
}
@media (max-width: 576px) {
  .header-img {
    height: 50vh;
    margin: 0;
  }
  .top-right {
    display: none;
  }

  .green {
    position: absolute;
    left: 3rem;
    top: 30vh;
    height: 40vh;
    width: 55vh;
  }
  .form-phone {
    display: inline-block;
    margin-right: 5px;
    margin-left: 5px;
  }
  .paragraph {
    font-size: 15px;
    font-weight: 300;
  }
  .list {
    font-size: 15px;
  }
  ::-webkit-input-placeholder {
    font-size: 16px;
  }
  :-ms-input-placeholder {
    font-size: 16px;
  }
  ::placeholder {
    font-size: 16px;
  }
  .small {
    font-size: 15px;
  }
  .nav1 {
    font-size: 20px;
  }
  .heading {
    font-size: 23px;
  }
  .header-text {
    font-size: 25px;
  }
  .btn-primary {
    font-size: 14px;
    width: 10rem;
    height: 2.6rem;
    text-shadow: none;
    margin: 3px;
  }
  .flex-container {
    flex-direction: column;
    align-items: center;
  }
  .fontSize {
    font-size: 12px;
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  }
  #navbar {
    display: none;
  }
  #menu {
    right: 0.1px;
    top: 70%;
    width: 8em;
    color: #25d366;
    position: fixed;
    z-index: 1;
  }

  #bottomNav {
    flex-direction: column;
    display: inline;
    background-color: #47484c;
    color: #25d366;
  }
  #bottomNav a {
    color: #25d366;
  }

  .batteryProd {
    height: 50vh;
    width: 50vh;
  }
  footer {
    position: relative;
    padding-bottom: 150px;
    left: 0;
    bottom: 0;
    width: 100%;
  }
  .battery {
    font-size: 20px;
  }
  .glyph {
    font-size: 30px;
  }
  .blog-img {
    height: 175px;
    width: 100%;
  }
  .card {
    margin-left: 0px;
  }
}

@keyframes mymove {
  from {
    left: 0px;
  }
  to {
    left: 200px;
  }
}
@keyframes blinker {
  50% {
    opacity: 0;
  }
}

